  padding: 69px 0 54px 0;
}
.cart-foot-wrap {
  height: 54px;
  line-height: 54px;
  margin-top: 18px;
}
.cart-foot-wrap .cart-foot-l {
    float: left;
    font-size: 16px;
    padding: 0 22px;
}
.cart-foot-wrap .item-all-check {
    float: left;
    color: #EE7A23;
}
.cart-foot-wrap .item-all-check .item-check-btn {
      margin-right: 18px;
}
.cart-foot-wrap .item-all-check span {
      vertical-align: middle;
}
.cart-foot-wrap .item-all-del {
    float: left;
    margin-top: 2px;
}
.cart-foot-wrap .cart-foot-r {
    float: right;
}
.cart-foot-wrap .item-total {
    float: left;
    margin-right: 31px;
    color: #605F5F;
    font-size: 18px;
}
.cart-foot-wrap .item-total .total-price {
      margin-left: 16px;
      color: #d1434a;
      font-family: "moderat", sans-serif;
}
.cart-foot-wrap .next-btn-wrap {
    float: right;
}
.cart-foot-wrap:after {
    visibility: hidden;
    display: block;
    content: " ";
    clear: both;
}
/** order confirm **/
.checkout-order {
  padding: 69px 0 54px 0;
}
.confirm-item-list-wrap {
  margin-bottom: 124px;
}
.redeem-coupon-wrap {
  margin-bottom: 123px;
  text-align: center;
}
.redeem-coupon-wrap .text-redeem {
    width: 380px;
    height: 54px;
    line-height: 54px;
    margin-right: 16px;
    vertical-align: top;
}
.apply-promotion-wrap {
  margin-bottom: 124px;
}
.price-count-wrap {
  margin-bottom: 26px;
  padding-top: 62px;
  border-top: 1px solid #e9e9e9;
}
.order-foot-wrap .prev-btn-wrap {
  float: left;
}
.order-foot-wrap .next-btn-wrap {
  float: right;
}
.order-foot-wrap:after {
  visibility: hidden;
  display: block;
  content: " ";
  clear: both;
}
@media only screen and (max-width: 1280px) {
.container {
    width: 100%;
}
}
@media only screen and (max-width: 991px) {
.cart {
    padding-top: 40px;
}
.cart-foot-wrap .cart-foot-l {
    padding: 0 10px;
}
}
@media only screen and (max-width: 767px) {
.checkout {
    background: #f0f0f0;
}
.checkout-title {
    margin: 0 10px 13px 10px;
}
.checkout-title span {
      font-size: 14px;
      background: #f0f0f0 !important;
}
.next-btn-wrap {
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: 101;
}
.next-btn-wrap .btn {
      display: block;
      width: 100%;
}
.checkout-addr .next-btn-wrap {
    padding: 0;
    border: none;
}
.cart, .checkout-order {
    padding-top: 13px;
}
.cart-foot-wrap {
    height: 42px;
    line-height: 42px;
    margin: 0;
}
.cart-foot-wrap .cart-foot-l {
      display: none;
}
.cart-foot-wrap .item-total {
      float: none;
      margin-right: 10px;
      font-size: 16px;
}
.confirm-item-list-wrap, .redeem-coupon-wrap, .apply-promotion-wrap {
    margin-bottom: 23px;
}
.redeem-coupon-wrap .redeem-coupon {
    margin: 0 10px;
}
.redeem-coupon-wrap .text-redeem {
    width: 100%;
    margin-bottom: 8px;
}
.redeem-coupon-wrap .btn {
    width: 100%;
}
.price-count-wrap {
    margin: 0;
    padding-top: 0;
    border: none;
}
.tee_tips_pop {
    display: none;
}
.order-foot-wrap .prev-btn-wrap {
    display: none;
}
.addr-list-wrap {
    padding-bottom: 23px;
}
}
/** 订单详情 **/

.md-modal {
    position: fixed;
    top: 50%;
    left: 50%;
    width: 535px;
    height: auto;
    -webkit-transform: translate(-50%,-50%);
    transform: translate(-50%,-50%);
    visibility: hidden;
    z-index: 201;
}
.md-modal-transition.md-show .md-modal-inner {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;
    -webkit-transition: all .5s ease-out;
    transition: all .5s ease-out;
}
.md-modal-transition .md-modal-inner {
    background: #fff;
    -webkit-transform: translateY(20%);
    transform: translateY(20%);
    opacity: 0;
    -webkit-transition: all .3s ease-out;
    transition: all .3s ease-out;
}
.md-modal .md-modal-inner {
    padding: 60px 50px;
}
.md-modal .confirm-tips {
    min-height: 5.65em;
}
.md-modal .confirm-tips, .md-modal .alert-tips {
    font-size: 14px;
    font-weight: 200;
    text-align: center;
}
.md-modal .btn-wrap {
    margin-top: 20px;
    text-align: center;
    font-size: 0;
}
.md-modal .btn-wrap .btn {
    width: 45%;
    min-width: 80px;
    margin: 0 2.5%;
}
.md-modal .md-close {
    position: absolute;
    top: 7px;
    right: 7px;
    width: 34px;
    height: 34px;
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
    text-indent: -8000px;
}
.md-modal .md-close:hover:before {
    -webkit-transform: rotate(-135deg);
    transform: rotate(-135deg);
}
.md-modal .md-close:hover:before, .md-modal .md-close:hover:after {
    -webkit-transition: -webkit-transform .3s ease-out;
    transition: -webkit-transform .3s ease-out;
    transition: transform .3s ease-out;
    transition: transform .3s ease-out, -webkit-transform .3s ease-out;
    transition: transform .3s ease-out,-webkit-transform .3s ease-out;
}
.md-modal .md-close:before {
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
}
.md-modal .md-close:before, .md-modal .md-close:after {
    position: absolute;
    top: 16px;
    left: -4px;
    content: "";
    width: 44px;
    height: 3px;
    background: #605f5f;
    -webkit-transition: -webkit-transform .5s ease-out;
    transition: -webkit-transform .5s ease-out;
    transition: transform .5s ease-out;
    transition: transform .5s ease-out, -webkit-transform .5s ease-out;
    transition: transform .5s ease-out,-webkit-transform .5s ease-out;
}
.md-modal .md-close:hover:after {
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
}
.md-modal .md-close:after {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}
